body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@font-face {
    font-family: 'Alfarn';
    font-style: normal;
    font-weight: 400;
    src: local('Alfarn'),
          url('./components/font/Alfarn.otf') format('opentype');
  }
  
  @font-face {
    font-family: 'Corporative';
    font-style: normal;
    font-weight: 400;
    src: local('Corporative'),
          url('./components/font/Corporative.otf') format('opentype');
  }
  
code {
    font-family: "Corporative",Corporative,  "Alfarn", Alfarn, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}





@media (min-width: 320px) and (min-height: 480px){
    .EscritorioContainer{
      border: 1px solid red;
      display: none; 
     }
  }
  